import ButtonDelete from "../Components/Buttons/ButtonDelete"
import ButtonEdit from "../Components/Buttons/ButtonEdit"
import Table from "../Components/Table"

const DataKeluargaAdmin = ({
    dataKeluarga,
    deleteKeluarga,
    type,
    setDataKeluarga
}: DataKeluargaT.DataKeluargaAdminT) => {
    const RowHead = (
        <tr>
            <th>No</th>
            <th>Nomor KK</th>
            <th>Nama Kepala Keluarga</th>
            <th>NIK Kepala Keluarga</th>
            <th colSpan={2}>ACTION</th>
        </tr>
    )

    return (
        <Table
            title="Tabel Data Keluarga"
            rowThead={RowHead}
            search={{
                url: "data-keluarga?search=:keyword",
                dispatch: setDataKeluarga
            }}
        >
            {dataKeluarga &&
                dataKeluarga.map(
                    ({ no_kk, kepala_keluarga, nik_kepala_keluarga }, i) => (
                        <tr key={i}>
                            <td className="text-bold-500">{++i}</td>
                            <td>{no_kk}</td>
                            <td>{kepala_keluarga.nama}</td>
                            <td className="text-bold-500">
                                {nik_kepala_keluarga}
                            </td>
                            <td className="d-flex justify-content-center align-items-center gap-2">
                                <ButtonEdit
                                    to={`/${type}/data-keluarga/${no_kk}`}
                                />
                                <ButtonDelete
                                    props={{
                                        onClick: () =>
                                            deleteKeluarga(
                                                nik_kepala_keluarga,
                                                no_kk
                                            )
                                    }}
                                />
                            </td>
                        </tr>
                    )
                )}
        </Table>
    )
}

export default DataKeluargaAdmin
